@import url(https://fonts.googleapis.com/css2?family=PT+Sans:ital@1&display=swap);
@import url(https://fonts.googleapis.com/css2?family=PT+Sans:ital@1&family=Roboto:ital,wght@1,100&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'PT Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

#main, nav, footer {
    background-color: #7f8c8d;
}

#main {
    height: 80vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
}

nav, footer {
    height: 10vh;
}

.navbar-container {
    display: flex;
    align-items: center;
    justify-content: center;
}

.navbar {
    display: flex;
    list-style-type: none;
    margin: 0;
    padding: 0;
    height: 45px;
    justify-content: center;
    align-items: center;
}

.navbar-item > a {
    color: white;
    text-decoration: none;
    font-weight: 700;
    letter-spacing: 1px;
    font-size: 16px;
    padding: 10px;
}

.navbar-item > a:hover {
    background-color: white;
    color: #7f8c8d;
    border-radius: 8px;
}

.box0 {
  background-color: #f39c12;
  width: 350px;
  height: auto;
  color: white;
  padding: 20px;
  border-radius: 20px;
  text-align: center;
  box-shadow: 0 10px 20px #000000;
}

.inputField0 {
  padding: 10px;
  font-size: 125%;
  border-radius: 25px;
  background-color: #f1c40f;
  color: white;
  text-align: center;
  border: 2px solid #f1c40f;
}

.inputField0:focus {
  outline: none;
}

.errorMsg0 {
  text-align: center;
}

@media (max-width: 350px) {
  .box0 {
    width: 100%;
  }
  .inputField0 {
    width: 100%;
  }
}

.box1 {
    background-color: #16a085;
    width: 350px;
    height: auto;
    color: white;
    padding: 20px;
    border-radius: 20px;
    text-align: center;
    box-shadow: 0 10px 30px #000000;
}

.inputField1 {
    padding: 10px;
    font-size: 125%;
    border-radius: 25px;
    background-color: #1abc9c;
    color: white;
    text-align: center;
    border: 2px solid #16a085;
}

.inputField1:focus {
    outline: none;
}

.errorMsg1 {
    text-align: center;
}

@media (max-width: 350px) {
    .box1 {
        width: 100%;
    }
    .inputField1 {
        width: 100%;
    }
}

.active_class > a {
    margin: 10px;
    padding: 20px;
    border-radius: 10px;
    border: none;
    color: white;
    text-decoration: none;
    font-weight: 700;
    letter-spacing: 1px;
    font-size: 16px;
}

.or > a {
    background-color: #f39c12;
}

.or > a:hover {
    background-color: #f1c40f;
}

.bl > a {
    background-color: #16a085;
}

.bl > a:hover {
    background-color: #1abc9c;
}
